import React from 'react';

import { WithCursor } from '../Cursor';

const ExternalLink = (props: { href: string; className?: string; children?: React.ReactNode; style?: any }) => {
  return (
    <WithCursor>
      <a
        href={props.href}
        target="_blank"
        rel="nofollow noopener noreferrer"
        className={props.className}
        style={props.style}
      >
        {props.children}
      </a>
    </WithCursor>
  );
};

export default ExternalLink;
