import React, { useContext } from 'react';
import { CursorContext } from './context';

export const CURSOR_ATTRIBUTE = 'data-cursor-style';

export const WithCursor = (props: { children: React.ReactElement }) => {
  const { actionsRef } = useContext(CursorContext);

  const show = () => {
    actionsRef.current.show();
  };
  const hide = () => {
    actionsRef.current.hide();
  };

  return React.cloneElement(props.children, { onMouseEnter: show, onMouseLeave: hide, style: { cursor: 'none' } });
};
