import React, { useRef, useEffect } from 'react';
import { throttle } from './throttle';

export type Actions = {
  show: () => void;
  hide: () => void;
};

export const Cursor = (props: { actionsRef: React.MutableRefObject<Actions> }) => {
  const cursor = useRef(null);

  useEffect(() => {
    const [moveCursor, cancelMoveCursor] = throttle((e: MouseEvent) => {
      const x = e.clientX;
      const y = e.clientY;

      if (cursor.current) {
        cursor.current.style.transform = `translate(${x}px, ${y}px)`;
      }
    });

    const show = () => {
      if (cursor.current) {
        cursor.current.style.display = 'block';
      }
    };
    const hide = () => {
      if (cursor.current) {
        cursor.current.style.display = 'none';
      }
    };
    props.actionsRef.current = { show, hide };

    document.addEventListener('mousemove', moveCursor);

    return () => {
      document.removeEventListener('mousemove', moveCursor);
      cancelMoveCursor();
    };
  }, []);

  return <div ref={cursor} className="cursor-circle cursor-hover cursor-enabled cursor" style={{ display: 'none' }} />;
};

export default Cursor;
