import React, { useEffect, useState } from 'react';
import ExternalLink from '../ExternalLink';
import InternalLink from '../InternalLink';
import { ToggleContainer, ToggleLine } from './MobileMenu.styles';

export const ToggleMobileMenu = (props: { show: boolean; onClick: () => void }) => {
  return (
    <ToggleContainer {...props}>
      <ToggleLine show={props.show} />
      <ToggleLine show={props.show} />
      <ToggleLine show={props.show} />
    </ToggleContainer>
  );
};

export const MobileMenu = (props: { show: boolean; toggleMenu: () => void }) => {
  const [visibility, setVisibility] = useState<'visible' | 'hidden'>(props.show ? 'visible' : 'hidden');
  const [opacity, setOpacity] = useState(props.show ? 1 : 0);

  useEffect(() => {
    if (props.show) {
      setVisibility('visible');
      setOpacity(1);
    } else {
      setOpacity(0);
      setTimeout(() => setVisibility('hidden'), 200);
    }
  }, [props.show]);

  return (
    <div
      className="navbar navbar-mobile nms navbar-dark"
      style={{ display: 'block', position: 'fixed', visibility, opacity, transition: 'opacity 0.2s ease-in-out' }}
    >
      <div className="navbar-head">
        <div className="container">
          <InternalLink to="/" className="navbar-brand">
            {' '}
            Andrei Alikimovich{' '}
          </InternalLink>
          <ToggleMobileMenu show={props.show} onClick={props.toggleMenu} />
        </div>
      </div>
      <div className="container">
        <div className="row gh-1 justify-content-center">
          <div className="col-12 col-md-7 col-lg-5 col-xl-4">
            <div className="navbar-body">
              <ul className="nav navbar-nav">
                <li className="nav-item">
                  <InternalLink to="/" className="nav-link" activeClassName="active">
                    <span className="nav-link-name">Home</span>
                  </InternalLink>
                </li>
                <li className="nav-item">
                  <InternalLink to="/about" className="nav-link" activeClassName="active">
                    <span className="nav-link-name">About</span>
                  </InternalLink>
                </li>
                <li className="nav-item">
                  <ExternalLink href="https://www.behance.net/alikimovich" className="nav-link">
                    <span className="nav-link-name">Behance</span>
                  </ExternalLink>
                </li>
                <li className="nav-item">
                  <ExternalLink href="https://www.linkedin.com/in/alikimovich/" className="nav-link">
                    <span className="nav-link-name">LinkedIn</span>
                  </ExternalLink>
                </li>
              </ul>
            </div>
          </div>
          <div className="col-md-1 d-none d-md-block"></div>
        </div>
      </div>
    </div>
  );
};
