export const throttle = <T_A, T_R>(f: (...args: T_A[]) => T_R) => {
  let token = null,
    lastArgs: T_A[] = null;

  const invoke = () => {
    f(...lastArgs);
    token = null;
  };

  const action = (...args: T_A[]) => {
    lastArgs = args;
    if (!token) {
      token = requestAnimationFrame(invoke);
    }
  };

  const cancel = () => token && cancelAnimationFrame(token);

  return [action, cancel];
};
