import React, { useState } from 'react';

import { CenterLogo } from './CenterLogo';

import InternalLink from '../InternalLink';
import LogoIcon from './icons/logo.inline.svg';
import { DesktopMenu } from './DesktopMenu';
import { ToggleMobileMenu, MobileMenu } from './MobileMenu';

export const AppBar = () => {
  const [showMenu, setShowMenu] = useState(false);

  return (
    <>
      <CenterLogo />
      <header className="navbar navbar-top navbar-expand-lg navbar-dark navbar-fixed">
        <div className="container">
          <span className="navbar-brand">
            <InternalLink to="/">
              <span className="d-lg-none" style={{ marginRight: '10px' }}>
                <LogoIcon />
              </span>
              Andrei Alikimovich
            </InternalLink>
          </span>

          <DesktopMenu />
          <ToggleMobileMenu show={showMenu} onClick={() => setShowMenu(v => !v)} />
        </div>
      </header>
      <MobileMenu show={showMenu} toggleMenu={() => setShowMenu(v => !v)} />
    </>
  );
};

export default AppBar;
